@use 'base/variables' as v;
@use 'base/mixins' as m;

.dashboard{
    background-color: v.$grisClaro;

    @include m.mediaq(v.$tablet){
        display: flex;
        height: 100vh;
    }

    .principal{
        flex:1;

        
    }

    .contenido{
        padding: 2rem;

        .nombre-pagina{
            font-size: 2.8rem;
            text-align: center;
        }
    }

    .formulario{
        input:not([type="submit"]){
            background-color: v.$blanco;

        }

        input[type="submit"]{
            @include m.boton (v.$indigo, v.$blanco);
            padding: 1.3rem;
            text-transform: uppercase;
            
            
        }
    }
}